<template>
  <div>
    <a-page-header
      style="border: 1px solid rgb(235, 237, 240)"
      :title="title"
      @back="() => $router.go(-1)"
    />
    <a-list
      size="large"
      :pagination="pagination"
      :data-source="messageList"
      :bordered="true"
    >
    <a-list-item slot="renderItem" slot-scope="item">
      <a-comment :author="item.user_name" :avatar="'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png'">
        <div slot="content">
          <p class="p-3 text-xl">{{ item.content }}</p>
          <div class="p-3" v-for="imagePath in item.files" :key="imagePath">
            <img  :src="imagePath" />
          </div>
        </div>
        <a-tooltip slot="datetime" :title="item.createdAt">
          <span>{{ item.createdAt}}</span>
        </a-tooltip>
      </a-comment>

    </a-list-item>
    </a-list>
    <!-- 评论 -->
    <a-alert
      v-if="visible"
      message="请输入内容"
      type="warning"
      closable
      :after-close="handleClose"
    />
    <a-textarea
      v-model="value"
      placeholder="Controlled autosize"
      :auto-size="{ minRows: 3, maxRows: 5 }"
    />
    <div class="clearfix">
      <a-upload
        action="http://localhost:3000/uploadImg"
        list-type="picture-card"
        :file-list="fileList"
        @preview="handlePreview"
        @change="handleChange"
      >
        <div v-if="fileList.length < 8">
          <a-icon type="plus" />
          <div class="ant-upload-text">上传图片</div>
        </div>
      </a-upload>

      <a-modal :visible="previewVisible" :footer="null" @cancel="handleCancel">
        <img alt="example" style="width: 100%" :src="previewImage" />
      </a-modal>
    </div>
    <!-- <a-upload
      list-type="picture"
      action="//jsonplaceholder.typicode.com/posts/"
      :preview-file="previewFile"
    >
      <a-button> <a-icon type="upload" /> Upload </a-button>
    </a-upload> -->
    <a-button class="float-right border" @click="sendMessage">评论 </a-button>
  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
function getBase64 (file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => resolve(reader.result)
    reader.onerror = (error) => reject(error)
  })
}

export default {
  props: ['title', 'contentId'],
  data () {
    return {
      moment,
      pagination: {
        onChange: (page) => {
          console.log(page)
        },
        pageSize: 50
      },
      messageList: [],
      visible: false,
      previewVisible: false,
      previewImage: '',
      value: '',
      path: 'http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4',
      fileList: [
      ]
    }
  },

  computed: {
    // contentData () {
    //   return this.$store.getters.getItemForIndex(this.$route.params.contentId)
    //     .content
    // },
    titleName () {
      return this.$store.getters.getItemForIndex(this.$route.params.contentId)
        .title
    }
  },
  watch: {
    $route: function () {
      console.log('router is change')
    }
  },
  methods: {
    handleCancel () {
      this.previewVisible = false
    },
    async handlePreview (file) {
      if (!file.url && !file.preview) {
        file.preview = await getBase64(file.originFileObj)
      }
      this.previewImage = file.url || file.preview
      this.previewVisible = true
    },
    handleChange ({ fileList }) {
      this.fileList = fileList
      console.log(fileList)
    },
    handleClose () {
      this.visible = false
    },
    sendMessage () {
      console.log(this.$store.getters.getUserId)
      if (this.value === '') {
        this.visible = true
        return
      }
      const fileList = this.fileList.map((val) => { return val.response.filePath })
      axios.post(process.env.VUE_APP_APIPATH + process.env.VUE_APP_SENDMESSAGE, {
        content: this.value,
        topicId: this.$route.params.contentId,
        userId: this.$store.getters.getUserId,
        fileList: fileList
      })
      // this.$store.commit('sendMessage', {
      //   index: this.$route.params.contentId,
      //   message: this.value
      // })
      this.value = ''
    }
  },
  mounted () { /* 请求数据，操作dom , 放在这个里面  mounted */
    console.log('模板编译完成4')
    axios.post(process.env.VUE_APP_APIPATH + process.env.VUE_APP_GETMESSAGE, {
      topicId: this.$route.params.contentId
    }).then((response) => {
      this.messageList = response.data
      this.messageList.forEach((val) => {
        val.files = JSON.parse(val.files)
      })
      console.log(this.messageList)
    })
  }
}
</script>

